/* 스크롤 아래 */
svg + svg {
  margin-top: 2em;
}

svg {
  width: 30px;
  max-height: 40px;
  display: inline-block;
}

.arrow-down{
  position: absolute; bottom: 5%; left: 50%; -webkit-transform: translateX(-50%); transform: translateX(-50%);
}

.arrow-down path {
stroke: #111;
fill: transparent;
stroke-width: 1px;	
animation: arrow 2s infinite;
-webkit-animation: arrow 2s infinite; 
}

@-webkit-keyframes arrow
{
0% {opacity:0}
40% {opacity:.8}
80% {opacity:0}
100% {opacity:0}
}

@keyframes arrow
{
0% {opacity:0}
40% {opacity:.8}
80% {opacity:0}
100% {opacity:0}
}

.arrow-down path.a1 {
animation-delay:-1s;
-webkit-animation-delay:-1s;
}

.arrow-down path.a2 {
animation-delay:-0.5s;
-webkit-animation-delay:-0.5s; 
}

