.cloud-container{
  opacity: 0.6; position: absolute;
}

.cloud, .cloud::before, .cloud::after{
  border-radius: 35px; background: #fff;
}

.cloud {
  display: inline-block;
  vertical-align: top;
  width: 100px;
  height: 30px;
  border-radius: 50px 35px 35px 15px;
  position: relative;
}

.cloud:before,
.cloud:after {
  content:'';
  display: block;
  position: absolute;
}

.cloud:before {  
  width: 40px;
  height: 45px;
  left: 12px;
  bottom: 0;
}

.cloud:after {
  width: 40px;
  height: 50px;
  right: 16px;
  bottom: 0;
}

.cloud:nth-child(1){
  position: absolute;
  top: 300px;
  left: 400px;
}

.cloud:nth-child(2){
  position: absolute;
  top: 250px;
  left: -150px;
}

.cloud:nth-child(3){
  position: absolute;
  top: 180px;
  left: -450px;
}

.cloud:nth-child(4){
  position: absolute;
  top: 80px;
  left: -230px;
}

.cloud:nth-child(5){
  position: absolute;
  top: 50px;
  left: 230px;
}

.cloud:nth-child(6) {
  position: absolute;
  top: 140px;
  right: 100px;
}

.cloud:nth-child(7){
  position: absolute;
  top: 50px;
  left: -700px;
}

.cloud:nth-child(8){
  position: absolute;
  top: 80px;
  left: -230px;
}

.cloud:nth-child(9){
  position: absolute;
  top: 200px;
  left: 700px;
}

.cloud:nth-child(10) {
  position: absolute;
  top: 300px;
  right: 500px;
}

.cloud {
    -webkit-animation: cloudmove 50s linear infinite both;
            animation: cloudmove 50s linear infinite both;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes cloudmove {
  0% { -webkit-transform: translateX(-450px); transform: translateX(-450px); }
  100% { -webkit-transform: translateX(1090px); transform: translateX(1090px); }
}

@keyframes cloudmove {
  0% { -webkit-transform: translateX(-450px); transform: translateX(-450px); }
  100% { -webkit-transform: translateX(1090px); transform: translateX(1090px); }
}


