.clickMe{
  line-height: 30px; font-size: 15px;
  position: absolute; top: 35%; right: 10%; z-index: 999;
  width: 115px; text-align: center; background-color: #fff;
  border-radius: 15px;
  -webkit-animation: click-in-right 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) both, clickmove 3s 1s infinite linear forwards;
          animation: click-in-right 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) both, clickmove 3s 1s infinite linear forwards;

  box-shadow: 0 1px 2px rgb(0 0 0 / 7%), 0 2px 4px rgb(0 0 0 / 7%), 0 4px 8px rgb(0 0 0 / 7%), 0 8px 16px rgb(0 0 0 / 7%), 0 16px 32px rgb(0 0 0 / 7%), 0 32px 64px rgb(0 0 0 / 7%);
}

@-webkit-keyframes clickmove{ 
  0%{ -webkit-transform:translate(-17%,-35%); transform:translate(-17%,-35%);} 
  50%{ -webkit-transform:translate(-17%,-120%); transform:translate(-17%,-120%);} 
  100%{ -webkit-transform:translate(-17%,-35%); transform:translate(-17%,-35%);}
}

@keyframes clickmove{ 
  0%{ -webkit-transform:translate(-17%,-35%); transform:translate(-17%,-35%);} 
  50%{ -webkit-transform:translate(-17%,-120%); transform:translate(-17%,-120%);} 
  100%{ -webkit-transform:translate(-17%,-35%); transform:translate(-17%,-35%);}
}

@-webkit-keyframes click-in-right {
  0% {
    -webkit-transform: translate(1000px,-35%);
            transform: translate(1000px,-35%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate(-17%, -35%);
            transform: translate(-17%, -35%);
    opacity: 1;
  }
}

@keyframes click-in-right {
  0% {
    -webkit-transform: translate(1000px,-35%);
            transform: translate(1000px,-35%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate(-17%, -35%);
            transform: translate(-17%, -35%);
    opacity: 1;
  }
}

.clickMe::before{
  content: '';
  position: absolute; top: 10px; left: -5px;
  border-top: 0px solid transparent;
  border-right: 0px solid transparent;
  border-left: 15px solid transparent;
  border-bottom: 15px solid #fff;
}

