	width: 95px;
	/* height: 80px; */
	border-radius: 50px;
	display: block;
	padding: 10px;
	box-shadow: inset 5px 5px 20px rgba(0,0,0,0.5);
	z-index: 5;
	position: absolute;
	bottom: 30px;
	right: 30px;
	cursor: pointer;
	cursor: hand;
  transition: all 0.5s;
  margin-right: 70px;
}

